@import url(https://fonts.googleapis.com/css2?family=Cabin&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Life+Savers&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Robert+Mono&display=swap);
/* @import url('https://fonts.googleapis.com/css2?family=Abril+Fatface&display=swap'); */
/* @import url('https://fonts.googleapis.com/css2?family=Concert+One&display=swap'); */

:root {
  --color1: steelblue;
}
body {
  width:100%;  
  height:96vh;
  background:white;
  position:absolute;
  top:0;left:0;
  margin:0;padding:0;border:0;
  /* padding-top:2vh; */
  /* padding-bottom:2vh; */
  font-family:'Cabin';
  overflow:hidden;font-weight:600;

}
.Body{
  background:white;width:100%;text-align:center;height:100vh;width:100%;margin-top:0;background:white;
  background: linear-gradient(
    to bottom, 
    steelblue 0%,
    steelblue 100vh,
    #000000 20vh,
    white 20vh
  );
  background: linear-gradient(
    to bottom, 
    var(--color1) 0%,
    var(--color1) 100vh,
    #000000 20vh,
    white 20vh
  );
  background:steelblue;
  background:var(--color1);
  opacity:1;
}
.Content{
  width:100%;
  height:100vh;
}
div,p,button,h1,h2,h3,h4,h5,input{
  margin:0;vertical-align:top;border:0;font-family:'Cabin';border-radius:0;overflow:hidden;font-weight:600;
}
input{
  border:0; outline:none;vertical-align:bottom;
}
h1{ padding:0 0 20px 0;}
h2,h3,h4,h5{ padding:0 0 10px 0;font-weight:600;}
div,p,button{
  padding:0;
}
button{
  cursor:pointer;outline:none;padding:5px;font-weight:550;
}
p{
  font-size:12px;margin:0;padding:0;font-weight:550;
}
input[type="text"]{
  font-weight:550;
}
.BodyInner{
  max-width:1800px;display:inline-block;text-align:left;
}
.MainButton{
  padding:10px;border-radius:5px;background:transparent;font-size:22px;
  background:linear-gradient(gainsboro,whitesmoke 200%);  
  margin:0 10px 10px 0;
}
.MainButton:hover{
  opacity:0.7;
  -webkit-animation:OpacityChange 0.5s;
          animation:OpacityChange 0.5s;
}
@-webkit-keyframes OpacityChange{
  0%{
    opacity:1;
  }
  100%{
    opacity:0.7;
  }
}
@keyframes OpacityChange{
  0%{
    opacity:1;
  }
  100%{
    opacity:0.7;
  }
}
.MainButton:last-child{
  margin-right:0;
}
.Row{
  width:100%;
}
.StandardRow{
  width:100%;overflow:hidden;text-align:left;
}
.StandardCenteredRow{
  text-align:center;width:100%;
}
.Top{
  width:100%;text-align:center;margin-bottom:20;opacity:0.95;
  position:fixed;top:0;left:0;
  height:25;padding:10px 0 10px 0;
  background:white;
}
.Top h2{
  display:inline-block;width:auto;margin:0;padding:0;
}
.LogoutButton{
  position:fixed;
  top:0;left:0;
  z-index:5;
}


.TextArea{
    border:0; font-size:17px;background:transparent;
    line-height:25px;
    /* border-bottom:2px grey solid; */
    width:calc(100% - 65px);
    padding:10px 5px 5px 10px;
    margin:0;
    outline:none;
 
    resize:none;
    background:linear-gradient(whitesmoke -100%,gainsboro);
    background:linear-gradient(whitesmoke,gainsboro 200%);
    min-height:25px;
    vertical-align:bottom;overflow:hidden;
    overflow:visible;font-family:'Cabin';

    z-index:0;

}
.TextArea:hover{
    box-shadow:0 0 1px 3px grey;
    -webkit-animation:TextAreaHover 0.2s;
            animation:TextAreaHover 0.2s;
}
@-webkit-keyframes TextAreaHover{
    0%{
        box-shadow:0 0 0 0 transparent;
    } 100%{
        box-shadow:0 0 1px 3px grey;
    }
}
@keyframes TextAreaHover{
    0%{
        box-shadow:0 0 0 0 transparent;
    } 100%{
        box-shadow:0 0 1px 3px grey;
    }
}
.TextArea:focus{
    -webkit-animation:ColorInput 0.3s;
            animation:ColorInput 0.3s;
    box-shadow:0 0 1px 3px steelblue;
    /* background:linear-gradient(gainsboro -200%,whitesmoke); */
    background:linear-gradient(whitesmoke,white);
    color:royalblue;
}
@-webkit-keyframes ColorInput{
    0%{
        color:black;
        background:linear-gradient(whitesmoke,gainsboro 200%);
    }
    100%{
        color:royalblue;
        background:linear-gradient(whitesmoke,white);
    }
}
@keyframes ColorInput{
    0%{
        color:black;
        background:linear-gradient(whitesmoke,gainsboro 200%);
    }
    100%{
        color:royalblue;
        background:linear-gradient(whitesmoke,white);
    }
}
.Creator label{
    display:block;margin-bottom:10px;
}
.Creator input[type="text"]{
    display:block;
    background:whitesmoke;
    outline:none;
    width:200px;
    border-radius:5px;
    box-shadow:0 0 1px 1px lightgrey;
    padding:7px 5px 7px 5px;
}
.BackwardsButtonInner{
    background:coral;border-radius:0 4px 4px 0;
    display:inline-block;  width:30px; 
    height:10px;     padding:5px; letter-spacing:1px;
}
.BackwardsButtonArrow{
  display:inline-block;border-radius:0 3px 3px 0;border-radius:0;
  width: 0; 
  height: 0; 
  border-top: 10px solid transparent;
  border-bottom: 10px solid transparent; 
  border-right:10px solid coral; 
}
.BackwardsButton{
    margin-top:10px;
}
.GoalOverview .AddButton{
    padding:3px 0 3px 0;margin:0;overflow:visible;
    color:darkgreen;background:transparent;
    border-radius:4px;
}
.GoalOverview .AddButton:hover{
    padding:3px 5px 3px 5px;color:black;
    -webkit-animation:AddButtonHover 0.5s;
            animation:AddButtonHover 0.5s;background:rgb(180, 251, 180);
}
@-webkit-keyframes AddButtonHover{
    0%{color:darkgreen;margin-top:0;padding:3px 0 3px 0;}
    100%{color:black;background:rgb(180, 251, 180);padding:3px 5px 3px 5px;}
}
@keyframes AddButtonHover{
    0%{color:darkgreen;margin-top:0;padding:3px 0 3px 0;}
    100%{color:black;background:rgb(180, 251, 180);padding:3px 5px 3px 5px;}
}
.AddButton .Icon{
    display:inline-block;vertical-align:top;
    padding:0;
}
.AddButton p{
    margin-right:5px;font-weight:600;
}
.DeleteButton{
    background:linear-gradient(#ED213A,#93291E);display:inline-block;
}
.DeleteButton p{
    display:inline-block;margin-right:5px;
}
.DeleteIcon{
    display:inline-block;cursor:pointer;
}
.SaveButton{
    width:44px; padding:2px 4px 0 4px;border-radius:4px;cursor:pointer;display:inline-block;
}
.SaveButton p{
    display:inline-block;margin-right:5px;font-size:10px;
}
.SaveIcon{
    display:inline-block;padding-top:2px;vertical-align:top;cursor:pointer;
}



.LongTermPlanning{
    width:calc(100% - 20px);position:fixed;top:0;margin-top:0;left:0;overflow-y:auto;height:calc(100vh);text-align:center;
    padding:0 10px 0 10px;   
}
.LongTermPlanningTop{
    width:100%;text-align:center;margin-top:80px;
}
.LongTermPlanningTopInner{
    margin:0;text-align:left;max-width:850px;width:calc(100%);overflow:hidden;display:inline-block;
    border-radius:0;background:white;padding-bottom:80px;
}
.LongTermPlanningInner{
    
    margin:0;text-align:left;display:inline-block;max-width:850px;width:calc(100%);overflow:hidden;
    border-radius:0;background:white;padding-bottom:80px;position:relative;top:10vh;overflow-y:auto;height:calc(90vh - 80px);
}
.LongTermPlanning .Border{
    height:20px; width:100%; background:linear-gradient(#222222,#666666);margin-bottom:30px;border-radius:100px 100px 100px 100px;
}
.LongTermPlanning button{
    display:inline-block;background:transparent;
}
.YearButton:hover,.MonthButton:hover{
    color:darkorange;
    -webkit-animation:ColorChange 0.3s;
            animation:ColorChange 0.3s;
}
.YearPlanning{
    width:100%;
    margin-bottom:10px;text-align:center;min-height:calc(100vh - 90px);
}
.YearPlanningInner{
    width:100%;max-width:550px;padding:30px 40px 20px 40px;display:inline-block;
}
@-webkit-keyframes ColorChange{
    0%{color:black}
    100%{color:darkorange}
}
@keyframes ColorChange{
    0%{color:black}
    100%{color:darkorange}
}
.YearButton{
    font-size:22px;font-weight:600;
    margin:0 20px 5px 0;overflow:visible;
}
.MonthButton{
    margin-right:0 5px 0 0;font-size:16px;overflow:visible;
}
.Top{
    width:90%;
    
    height:50px;padding:0;margin:0;background:transparent;text-align:left;
    padding:10px 5% 10px 5%;
}
.MainIcon{
    display:inline-block;
    background:red;border-radius:15px;
    height:30px;width:30px;
}
.LargeComponent{
    position:fixed;text-align:center;
    top:0;left:0;width:100%;height:100vh;
}
.LargeComponentInner{
    margin:0;text-align:left;max-width:1200px;width:calc(100%);overflow:hidden;display:inline-block;
    border-radius:0;background:white;padding-bottom:80px;height:calc(100% - 80px);
}

.Goal{
    display:inline-block;overflow:visible;border-radius:4px;
    padding:0 0 10px 0;font-family:'Concert One';
}
.Goal:last-child{
    padding-bottom:0;
}
.InputRow{
    width:calc(100%);display:block;padding:5px 0 5px 0;overflow:visible;text-align:left;
}

.CheckIconWrapper{
    display:inline-block;cursor:pointer;text-align:center;
    border-width:0;
    box-shadow:1px 1px 2px 2px #555555;
    color:darkgreen;
    width:calc(40.5px);
    height:40.5px;
    margin:0 0 0px 1px;
    border-radius:0 4px 4px 0;
    z-index:10;
    overflow:visible;
    vertical-align:bottom;
}
.CheckIcon{
    font-size:20; padding:7px 0 0 0;
}


.ActionRow{
    height:20px;vertical-align:top;padding:0 10px 0 10px;width:100%;text-align:left;
}
.ActionRow Button{
    margin:0;height:100%;font-size:14px;padding-top:0;
}
.GoalList{
    width:100%;overflow:visible;margin-bottom:10px;display:inline-block;margin-top:40px;
    text-align:center;padding:0;
    background:white;
    border-radius:2px;
}
.NoItemsComponent{
    display:block;font-weight:500;color:#444444;font-size:17px;text-align:center;
    width:100%;
}
.NoItemsComponent p{
    display:inline-block;
}

.GoalOverview{
    width:90%;padding-bottom:80px;text-align:center;
    padding-left:5%;padding-right:5%;
}
.GoalOverviewInner{
    margin:0;text-align:left;display:inline-block;max-width:900px;width:calc(100%);overflow:hidden;
    border-radius:0;background:white;padding-bottom:80px;
}
.GoalOverviewContent{
    padding-top:40px;max-width:400px;display:inline-block;
    padding-bottom:20px;
    width:calc(90%);overflow:hidden;
}

.GoalOverviewInner .StandardRow{
    width:100%;
    overflow:hidden;
}
.YearTitle{
    margin:0 5% 0 5%;width:90%;padding:0;margin-top:20px;
    border-bottom:5px steelblue solid;
}
.YearTitle h2{
    font-size:20px;font-weight:600;color:black;
}
.EndTitle{
    text-align:left;opacity:1;
    width:90%;
    border-bottom: 10px steelblue solid;
    padding:10px 0 5px 0;overflow:hidden;height:auto;margin:0 5% 0 5%;
}
.EndTitle h2{
    vertical-align:bottom;margin:0;padding:0;
}
.Evaluation {
    
}
.Evaluation{
    display:inline-block;vertical-align:bottom; overflow:hidden;text-align:left;   border-radius:0;
}   
.Evaluation p{
    font-weight:600;font-size:18px;margin-bottom:5px;
}
.ProgressMeter{
    text-align:right;
    width:100%;display:inline-block;vertical-align:bottom;
}
.ProgressMeter p{
    font-weight:600;font-size:18px;margin-bottom:5px;vertical-align:bottom;
}
.ProgressWrapper{
    width:calc(100% - 2px);border-radius:16px 0 0 16px;
    display:block;
    height:30px;
    background:red;

}
.Progress{
    background:linear-gradient(to right,steelblue,dodgerblue 200%);height:100%;border-radius:0;
}
.WeekOverview{
    width:90%;padding-left:5%;max-width:500px;
    -webkit-animation:ShowWeekOverview 0.3s;
            animation:ShowWeekOverview 0.3s;
}
@-webkit-keyframes ShowWeekOverview{
    0%{
        margin-top:50vh;
    }
    100%{
        margin-top:0vh;
    }
}
@keyframes ShowWeekOverview{
    0%{
        margin-top:50vh;
    }
    100%{
        margin-top:0vh;
    }
}
.WeekOverviewInner{
    text-align:left;display:inline-block;max-width:calc(500px);width:100%;overflow:hidden;
    border-radius:0;background:white;padding-bottom:80px;
}
.WeekOverviewContent{
    width:100%;
}
.Column p{
    display:block;width:60px;font-weight:500;font-size:15px;text-align:left;line-height:20px;
    color:royalblue;letter-spacing:2px;
}
.Week{
    margin-bottom:40px;padding-top:20px;border-top:5px steelblue solid;
}
.WeekDay{
    border-bottom:2px whitesmoke solid;padding-top:5px;padding-bottom:10px;
}
/* .WeekDay:nth-child(1){
    margin-top:15px;
} */
.Week h3{
    margin-bottom:10px;
}
.Column{
    display:inline-block;width:70px;
}
.Column2{
    display:inline-block;width:calc(100% - 70px);
}
.WeekDay .DayName{
    font-weight:700;color:#111111;font-size:16px;letter-spacing:0;
}
.WeeklyGoals{
   margin:30px 0 30px 0; 
}



.StickyList{
    width:90%;position:-webkit-sticky;position:sticky; top:0;text-align:center;vertical-align:bottom;
    overflow-x:auto;white-space: nowrap;text-align:left;
    padding-left:5%;padding-right:5%;
    background:white;padding-bottom:5px;
}
.ButtonList{
    background:transparent;opacity:1;padding:0;margin:0;padding-bottom:5px;
}
.ButtonList button{
    font-size:inherit;font-weight:600;text-align:left;font-size:22px;text-align:left;padding-right:0;
    display:inline-block;opacity:1;background:transparent;
}
.LoginComponent{
    font-family:'Cabin';width:calc(100% - 100px);margin:10vh 0 0 0;height:90vh;max-height:250px;;text-align:center;display:block;
    padding:0 40px 0 40px;
    border-left:10px white solid;border-right:10px white solid;
}
.LoginComponent .LoginForm{
    display:inline-block;min-width:250px;overflow:hidden;
}
.Create-Login-Button{
    font-size:16px;padding:0;background:transparent;color:orange;
}

.Create-User-Button{
    font-size:16px;padding:0;background:transparent;color:orange;
}


.LoginComponent input[type="text"],input[type="password"]{
    display:block;margin-bottom:5px;
}
.LoginComponent input[type="submit"]{
    background:transparent;color:lightgreen;padding:0;font-size:16px;font-weight:500;
}
.LoginComponent{
    text-align:left;font-size:14px;font-weight:600;
}
.LoginComponent label{
    font-family:'Cabin'; color:ghostwhite;font-weight:500;margin-bottom:3px;
}
